import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { InventoryService, IManagedObject, OperationStatus, IOperation } from '@c8y/client';
import { gettext } from '@c8y/ngx-components';
import { RepositoryService } from '../repository.service';
import { AlertService } from '@c8y/ngx-components';
import { DeviceConfigurationOperation } from '../repository.model';
import { DeviceConfigurationService } from './device-configuration.service';

@Component({
  selector: 'c8y-text-based-configuration',
  templateUrl: './text-based-configuration.component.html'
})
export class TextBasedConfigurationComponent implements OnInit {
  device: IManagedObject;
  latestOperation: IOperation;
  showTextBasedConfigReload: boolean;
  showTextBasedConfigSave: boolean;
  reloadingConfig = false;
  savingConfig = false;
  config: string;

  constructor(
    private route: ActivatedRoute,
    private alertService: AlertService,
    private repositoryService: RepositoryService,
    private deviceConfigurationService: DeviceConfigurationService,
    private inventoryService: InventoryService
  ) {}

  async ngOnInit() {
    this.device = this.route.snapshot.parent.data.contextData;
    await this.loadOperation();
    this.showTextBasedConfigReload = this.deviceConfigurationService.hasAnySupportedOperation(
      this.device,
      [DeviceConfigurationOperation.SEND_CONFIG]
    );
    this.showTextBasedConfigSave = this.deviceConfigurationService.hasAnySupportedOperation(
      this.device,
      [DeviceConfigurationOperation.CONFIG]
    );
    if (this.device.c8y_Configuration && this.device.c8y_Configuration.config) {
      this.config = this.device.c8y_Configuration.config;
    }
  }

  async loadOperation() {
    const operation = await this.repositoryService.getLastConfigUpdateOperation(this.device.id);
    if (operation !== null) {
      this.repositoryService.observeOperation(operation).subscribe(operationUpdate => {
        this.latestOperation = operationUpdate;
      });
    }
  }

  async reloadConfiguration() {
    this.reloadingConfig = true;
    const operationCfg = await this.repositoryService.createTextBasedConfigurationReloadOperation(
      this.device
    );
    try {
      this.repositoryService
        .createObservedOperation(operationCfg)
        .subscribe(
          operationUpdate => this.onOperationReloadSuccess(operationUpdate),
          operationUpdate => this.onOperationReloadError(operationUpdate),
          () => this.onOperationReloadComplete()
        );
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
  }

  async updateConfiguration(config) {
    this.savingConfig = true;
    const operationCfg = await this.repositoryService.createTextBasedConfigurationUpdateOperation(
      this.device,
      config
    );
    try {
      this.repositoryService
        .createObservedOperation(operationCfg)
        .subscribe(
          operationUpdate => this.onOperationUpdateSuccess(operationUpdate),
          operationUpdate => this.onOperationUpdateError(operationUpdate),
          () => this.onOperationUpdateComplete()
        );
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
  }

  private onOperationReloadSuccess(operationUpdate) {
    this.latestOperation = operationUpdate;
    if (operationUpdate.status === OperationStatus.PENDING) {
      this.alertService.success(gettext('Configuration will be reloaded.'));
    }
  }

  private onOperationReloadError(operationUpdate) {
    this.latestOperation = operationUpdate;
    this.reloadingConfig = false;
  }

  private async onOperationReloadComplete() {
    await this.loadDevice();
    this.config = this.device.c8y_Configuration.config;
    this.reloadingConfig = false;
  }

  private onOperationUpdateSuccess(operationUpdate) {
    this.latestOperation = operationUpdate;
    if (operationUpdate.status === OperationStatus.PENDING) {
      this.alertService.success(gettext('Configuration will be updated.'));
    }
  }

  private onOperationUpdateError(operationUpdate) {
    this.latestOperation = operationUpdate;
    this.savingConfig = false;
  }

  private onOperationUpdateComplete() {
    this.device.c8y_Configuration.config = this.config;
    this.savingConfig = false;
  }

  private async loadDevice() {
    this.device = (await this.inventoryService.detail(this.device.id, { withChildren: false })).data;
  }
}
